import React from "react";
import { injectIntl } from "react-intl";
import Page from "material-ui-shell/lib/containers/Page/Page";
import Scrollbar from "material-ui-shell/lib/components/Scrollbar";
import {
  Button,
  Card,
  CardContent,
  CardHeader,
  makeStyles,
  MenuItem,
  Paper,
  Step,
  StepContent,
  StepLabel,
  Stepper,
  TextField,
  Typography,
} from "@material-ui/core";

const useStyles = makeStyles((theme) => ({
  root: {
    width: "100%",
  },
  button: {
    marginTop: theme.spacing(1),
    marginRight: theme.spacing(1),
  },
  actionsContainer: {
    marginBottom: theme.spacing(2),
  },
  resetContainer: {
    padding: theme.spacing(3),
  },
}));

function getSteps() {
  return [
    "Basic Information Section",
    "Commute & Transportation Section",
    "Home & Energy Section",
    "Food & Consumption Section",
    "Waste & Recycling Section",
    "Environmental Awareness & Goals Section",
  ];
}

function getStepContent(step) {
  switch (step) {
    case 0:
      return (
        <div>
          <Card>
            <CardHeader subheader="Personal Details" />
            <CardContent>
              <form noValidate autoComplete="off">
                <div style={{ marginBottom: 16 }}>
                  <TextField
                    label="Name"
                    variant="outlined"
                    fullWidth
                    margin="normal"
                  />
                  <TextField
                    label="Email"
                    type="email"
                    variant="outlined"
                    fullWidth
                    margin="normal"
                  />
                  {/* Profile Picture Upload */}
                  <div style={{ margin: "16px 0" }}>
                    <input
                      accept="image/*"
                      style={{ display: "none" }}
                      id="profile-picture-upload"
                      type="file"
                    />
                    <label htmlFor="profile-picture-upload">
                      <Button variant="outlined" color="primary" component="span">
                        Upload Profile Picture
                      </Button>
                    </label>
                  </div>
                  <TextField
                    label="Location (City, State/Province, Country)"
                    variant="outlined"
                    fullWidth
                    margin="normal"
                  />
                  <TextField
                    select
                    label="Age Range"
                    variant="outlined"
                    fullWidth
                    margin="normal"
                    defaultValue=""
                  >
                    <MenuItem value="18-25">18-25</MenuItem>
                    <MenuItem value="26-35">26-35</MenuItem>
                    <MenuItem value="36-45">36-45</MenuItem>
                    <MenuItem value="46-55">46-55</MenuItem>
                    <MenuItem value="55+">55+</MenuItem>
                  </TextField>
                  <TextField
                    select
                    label="Household Size"
                    variant="outlined"
                    fullWidth
                    margin="normal"
                    defaultValue=""
                  >
                    <MenuItem value="1">1</MenuItem>
                    <MenuItem value="2">2</MenuItem>
                    <MenuItem value="3-4">3-4</MenuItem>
                    <MenuItem value="5+">5+</MenuItem>
                  </TextField>
                  <TextField
                    select
                    label="Housing Type"
                    variant="outlined"
                    fullWidth
                    margin="normal"
                    defaultValue=""
                  >
                    <MenuItem value="Apartment">Apartment</MenuItem>
                    <MenuItem value="House">House</MenuItem>
                    <MenuItem value="Condo">Condo</MenuItem>
                    <MenuItem value="Other">Other</MenuItem>
                  </TextField>
                </div>
              </form>
            </CardContent>
          </Card>
        </div>
      );
    case 1:
      return (
        <div>
          <Card>
            <CardHeader subheader="Work Commute (if applicable)" />
            <CardContent></CardContent>
          </Card>
          <Card>
            <CardHeader subheader="Travel Patterns" />
            <CardContent></CardContent>
          </Card>
        </div>
      );
    case 2:
      return (
        <div>
          <Card>
            <CardHeader subheader="Living Situation" />
            <CardContent></CardContent>
          </Card>
          <Card>
            <CardHeader subheader="Utility Consumption Estimates" />
            <CardContent></CardContent>
          </Card>
        </div>
      );
    case 3:
      return (
        <div>
          <Card>
            <CardHeader subheader="Dietary Preferences" />
            <CardContent></CardContent>
          </Card>
          <Card>
            <CardHeader subheader="Shopping & Consumption Habits" />
            <CardContent></CardContent>
          </Card>
        </div>
      );
    case 4:
      return (
        <div>
          <Card>
            <CardHeader subheader="Waste Management" />
            <CardContent></CardContent>
          </Card>
        </div>
      );
    case 5:
      return (
        <div>
          <Card>
            <CardHeader subheader="Current Knowledge Level" />
            <CardContent></CardContent>
          </Card>
          <Card>
            <CardHeader subheader="Personal Goals & Constraints" />
            <CardContent></CardContent>
          </Card>
        </div>
      );
    default:
      return "Unknown step";
  }
}

const ProfilePage = ({ intl }) => {
  const classes = useStyles();
  const [activeStep, setActiveStep] = React.useState(0);
  const steps = getSteps();

  const handleNext = () => {
    setActiveStep((prevActiveStep) => prevActiveStep + 1);
  };

  const handleBack = () => {
    setActiveStep((prevActiveStep) => prevActiveStep - 1);
  };

  const handleReset = () => {
    setActiveStep(0);
  };

  return (
    <Page pageTitle={intl.formatMessage({ id: "profile" })}>
      <Scrollbar
        style={{ height: "100%", width: "100%", display: "flex", flex: 1 }}
      >
        {/* {intl.formatMessage({ id: 'about' })} */}
        <Card>
          <CardContent>
            <div className={classes.root}>
              <Stepper activeStep={activeStep} orientation="vertical">
                {steps.map((label, index) => (
                  <Step key={label}>
                    <StepLabel>{label}</StepLabel>
                    <StepContent>
                      <Typography>{getStepContent(index)}</Typography>
                      <div className={classes.actionsContainer}>
                        <div>
                          <Button
                            disabled={activeStep === 0}
                            onClick={handleBack}
                            className={classes.button}
                          >
                            Back
                          </Button>
                          <Button
                            variant="contained"
                            color="primary"
                            onClick={handleNext}
                            className={classes.button}
                          >
                            {activeStep === steps.length - 1
                              ? "Submit"
                              : "Next"}
                          </Button>
                        </div>
                      </div>
                    </StepContent>
                  </Step>
                ))}
              </Stepper>
              {activeStep === steps.length && (
                <Paper square elevation={0} className={classes.resetContainer}>
                  <Typography>
                    Thanks for taking the time to complete your profile!
                  </Typography>
                  <Button onClick={handleReset} className={classes.button}>
                    Continue
                  </Button>
                </Paper>
              )}
            </div>
          </CardContent>
        </Card>
      </Scrollbar>
    </Page>
  );
};

export default injectIntl(ProfilePage);
